export interface ICookies
{
  cookieWarningDismissed: string;
  subscribed: string;
}

export class CookieServiceClass
{
  cookies: ICookies = {
    subscribed: "false",
    cookieWarningDismissed: "false"
  }
  constructor()
  {
    if(typeof window !== "undefined") //This prevents SSR from accessing document which is undefined during node build
    {
      this.cookies = this.getCookieObject();
      console.log(this.cookies);
    }
  }

  setCookie(name: string, value: string)
  {
    document.cookie = `${name}=${value};max-age=${60*60*24*365}`;
  }

  private getCookieObject() : any
  {
    let cookieObj = {};
    if(document.cookie)
    {
      document.cookie.split(";")
        .map(c => c.split("="))
        .forEach(c => {
          cookieObj[c[0].trim()] = c[1];
        });
    }
    return cookieObj;
  }
}

export const CookieService = new CookieServiceClass();